import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-rd-cash',
  templateUrl: './rd-cash.component.html',
  styleUrls: ['./rd-cash.component.scss']
})

export class RdCashComponent {
  @Input() detailsItem;
  ordersCashCollection = [];
  ngOnInit(): void {
    this.ordersCashCollection = this.detailsItem['ordersCashCollections']?.length && this.detailsItem['ordersCashCollections'] || this.detailsItem['ordersCashCollection'];
  }
}
