
import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from '../../../../../../../../services/header.service';
import { TransactionHistoryService } from '../../../../transaction-history-services/transaction-history.service';
import { ReceiptDetailsComponent } from '../../receipt-details/receipt-details.component';
import { Location } from '@angular/common';
@Component({
  selector: 'app-in-house-finance-details',
  templateUrl: './in-house-finance-details.component.html',
  styleUrls: ['./in-house-finance-details.component.scss']
})
export class InHouseFinanceDetailsComponent {
  financeDetails;
  isLoading = false;
  constructor(private route: ActivatedRoute,
    private modalService: NgbModal,
    private _location: Location,
    public headerService: HeaderService, private _transactionHistoryService: TransactionHistoryService
  ) { }
  ngOnInit(): void {
    this.headerService.setTitle('Transactions History > InHouse Finance Collection > View Collection Details');
    this.route.params.subscribe(param => {
      this.getInhouseCollectionDetails(param['id'])
    })
  }
  goBack() {
    this._location.back();
  }
  getInhouseCollectionDetails(orderCollectionId) {
    this._transactionHistoryService.getInhouseCollectionDetails(orderCollectionId).subscribe((res) => {
      console.log('Collection Details', res)
      this.financeDetails = res['data'];
    })
  }
  openModalPayment(orderCollectionId) {
    this.isLoading = true;
    this._transactionHistoryService.getOrderCollectionPaymentTypes(orderCollectionId).subscribe(response => {
      this._transactionHistoryService.getInhouseCollectionPaymentDetails(orderCollectionId).subscribe((res) => {
        this.isLoading = false;
        const modalRef = this.modalService.open(ReceiptDetailsComponent, { size: 'xl', });
        modalRef.componentInstance.detailsItem = this.financeDetails;
        modalRef.componentInstance.orderPaymentTypes = [...response.data];
        modalRef.componentInstance.collectionId = orderCollectionId;
        modalRef.componentInstance.paymentDetails = { ...res?.data };
      })
    })
  }

}
