import { Component, Input } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ViewUploadedFileComponent } from '../../../../../../main-fund/bank-deposits/view-uploaded-file/view-uploaded-file.component';

@Component({
  selector: 'app-rd-bank-transfer',
  templateUrl: './rd-bank-transfer.component.html',
  styleUrls: ['./rd-bank-transfer.component.scss']
})
export class RdBankTransferComponent {
  @Input() detailsItem;
  fileItem;
  fileType;
  fileTypeArr = ['png', 'jpeg', 'jpg', 'pdf'];
  constructor(private _modalService: NgbModal) { }
  ngOnInit(): void {
  }
  openFileModal(item, content) {
    this.fileType = this.getFileType(item);
    let file = this.fileTypeArr.includes(this.fileType) ? item : null;
    this._modalService.open(ViewUploadedFileComponent).componentInstance.file = file;
    //this.fileItem = item;
    //if (this.fileTypeArr.includes(this.fileType)) {
    //  this._modalService.open(content, {
    //    centered: true,
    //    size: 'md'
    //  })
    //}
  }
  getFileType(fileName) {
    return fileName?.split('.').pop();
  }
}
