import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from '../../../../../../../../services/header.service';
import { CollectService } from '../../../../../../cashier/collect/services/collect.service';
import { TreasuryService } from '../../../../../petty-cash-treasury/services/treasury.service';
import { TransactionHistoryService } from '../../../../transaction-history-services/transaction-history.service';
import { ReceiptDetailsComponent } from '../../receipt-details/receipt-details.component';
import { Location } from '@angular/common';

@Component({
  selector: 'app-view-details',
  templateUrl: './view-details.component.html',
  styleUrls: ['./view-details.component.scss']
})
export class ViewDetailsComponent {
  details;
  custmerName;
  collectionId = 0;
  isLoading = false;
  paymentList = [];
  pageNo = 1;
  remainingNotesList = [];
  totalRecordCount = 0;
  pnOrderId = 0;
  constructor(
    public _treasuryService: TreasuryService,
    public _transactionHistory: TransactionHistoryService,
    public modalService: NgbModal,
    public _collectService: CollectService,
    public _activeRoute: ActivatedRoute,
    public _headerService: HeaderService,
    public _location: Location,
  ) { }
  ngOnInit() {
    this._headerService.setTitle('Transactions History > Promissory Notes Collection > Pn Order Details');
    this._activeRoute.params.subscribe(params => {
      this.collectionId = params['id'];
      this.getPNOrderCollectionDetails();
    })
  }
  goBack() {
    this._location.back();
  }
  getPNOrderCollectionDetails() {
    this._treasuryService.getPNOrderCollectionDetails(this.collectionId).subscribe((response: any) => {
      this.details = response.data;
      this.paymentList = this.details?.listOfPNOrderCollections;
      this.pnOrderId = this.details.pnOrderId;
      this.getPNOrdersBooksPNS()
    })
  }
  getPNOrdersBooksPNS() {
    let params = `?PNOrderId=${this.pnOrderId}&isDue=${false}&PageNo=${this.pageNo - 1}&PageSize=6`;
    this._collectService.getPNOrdersBooksPNS(params).subscribe((response: any) => {
      this.remainingNotesList = response.data;
      this.totalRecordCount = response.totalRecordCount;
    })
  }
  pageChange(page) {
    this.pageNo = page
    this.getPNOrdersBooksPNS();
  }
  openModalPayment(id) {
    this.isLoading = true;
    this._transactionHistory.getPNOrderCollectionPayments(id).subscribe((paymentDetailsRes) => {
      this._transactionHistory.getOrderCollectionPaymentTypes(this.collectionId).subscribe(response => {
        console.log(paymentDetailsRes);
        console.log(response);
        if (response.isSuccess) {
          this.isLoading = false;
          const modalRef = this.modalService.open(ReceiptDetailsComponent, { size: 'xl', });
          modalRef.componentInstance.orderPaymentTypes = [...response.data];
          modalRef.componentInstance.paymentDetails = paymentDetailsRes.data;
          let details = {
            collectedBy: paymentDetailsRes.data['collectedBy'],
            collectionAt: paymentDetailsRes.data['collectionAt'],
            branch: paymentDetailsRes.data['branch'],
            register: paymentDetailsRes.data['register'],
          }
          modalRef.componentInstance.detailsItem = details;
          modalRef.componentInstance.collectionId = id;
        }
      })
    });
  }

}
