import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from '../../../../../../../../services/header.service';
import { TransactionHistoryService } from '../../../../transaction-history-services/transaction-history.service';
import { ReceiptDetailsComponent } from '../../receipt-details/receipt-details.component';
import { Location } from '@angular/common';

@Component({
  selector: 'app-under-taking-details',
  templateUrl: './under-taking-details.component.html',
  styleUrls: ['./under-taking-details.component.scss']
})
export class UnderTakingDetailsComponent {
  underTakingDetails;
  isLoading = false;
  constructor(private route: ActivatedRoute, private modalService: NgbModal,
    public headerService: HeaderService,
    public _location: Location,
    private _transactionHistoryService: TransactionHistoryService
  ) { }
  ngOnInit(): void {
    this.headerService.setTitle('Transactions History > Bank Undertaking Collection > View Collection Details');
    this.route.params.subscribe(param => {
      this.getBankUnderTakenCollectionDetails(param['id'])
    })
  }
  goBack() {
    this._location.back();
  }
  getBankUnderTakenCollectionDetails(orderCollectionId) {
    this._transactionHistoryService.getBankUnderTakenCollectionDetails(orderCollectionId).subscribe((res) => {
      console.log('Payment Collection Details', res)
      this.underTakingDetails = res['data'];
    })
  }
  openModalPayment(orderCollectionId) {
    this.isLoading = true;
    this._transactionHistoryService.getOrderCollectionPaymentTypes(orderCollectionId).subscribe(response => {
      this._transactionHistoryService.getBanksUnderTakingCollectionPaymentDetails(orderCollectionId).subscribe((res) => {
        this.isLoading = false;
        const modalRef = this.modalService.open(ReceiptDetailsComponent, { size: 'xl', });
        modalRef.componentInstance.detailsItem = this.underTakingDetails;
        modalRef.componentInstance.orderPaymentTypes = [...response.data];
        modalRef.componentInstance.collectionId = orderCollectionId;
        modalRef.componentInstance.paymentDetails = { ...res?.data };
      })
    })
  }

}
