import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CashDepositModalComponent } from 'src/app/dashboard/modules/main-fund/allocation/allocation/cash-deposit-modal/cash-deposit-modal.component';
import * as _ from 'underscore';
import { TransactionHistoryService } from '../../../../transaction-history-services/transaction-history.service';
import { HeaderService } from 'src/app/services/header.service';
import { DirectModalPaymentComponent } from '../direct-modal-payment/direct-modal-payment.component';
import { ReceiptDetailsComponent } from '../../receipt-details/receipt-details.component';
import { Location } from '@angular/common';

@Component({
  selector: 'app-view-direct-payments',
  templateUrl: './view-direct-payments.component.html',
  styleUrls: ['./view-direct-payments.component.scss'],
})
export class ViewDirectPaymentsComponent {
  searchText: string = '';
  sort: number = 1;
  totalRecords: number;
  pageNo: number = 0;
  pagin: number;
  pages: any[] = [];
  provideExpenses: any;
  viewDetailsDirectPayment: any;
  collectionsPayment: any[] = [];
  search: string;
  nameInitials: any;
  newName: any;
  isLoading = false;
  constructor(
    private route: ActivatedRoute,
    private router: Router,

    private TransactionHistoryService: TransactionHistoryService,
    private modalService: NgbModal,
    public headerService: HeaderService,
    public _location: Location,

  ) { }
  collectionId: string;
  ngOnInit(): void {
    this.collectionId = this.route.snapshot.paramMap.get('id');
    this.getDirectPaymentDetails(this.collectionId);
    this.headerService.setTitle(
      'Transactions History > Direct Payment Collection > View Collection Details'
    );
  }
  goBack() {
    this._location.back();
  }
  /**
   * get view details allocation
   */
  getDirectPaymentDetails(collectionId) {
    this.TransactionHistoryService.getDirectPaymentDetails(
      collectionId
    ).subscribe((response) => {
      console.log(response, 'view details');

      if (response.isSuccess == true) {
        this.viewDetailsDirectPayment = response.data;
        this.collectionsPayment = response.data.collections;
        this.totalRecords = response?.info?.totalRecordsCount;

        this.nameInitials =
          this.viewDetailsDirectPayment?.customer?.customerName;
        this.newName = this.nameInitials.split(' ');
        console.log(this.newName, 'name');
      }
    });
  }

  //openModalPayment(id) {
  //  this.TransactionHistoryService.getDirectPaymentCollectionPaymentDetails(id).subscribe((response) => {
  //    console.log(response, 'payment');
  //    if (response.isSuccess == true) {
  //      const modalRef = this.modalService.open(DirectModalPaymentComponent, {
  //        size: 'lg',
  //      });
  //      modalRef.componentInstance.detailsItem = response.data;
  //      modalRef.componentInstance.collectionId = id;
  //    }
  //  });
  //}
  openModalPayment(id) {
    this.isLoading = true;
    this.TransactionHistoryService.getDirectPaymentCollectionPaymentDetails(id).subscribe((paymentDetailsRes) => {
      this.TransactionHistoryService.getOrderCollectionPaymentTypes(this.collectionId).subscribe(response => {
        console.log(paymentDetailsRes, 'direct payment');
        if (response.isSuccess) {
          this.isLoading = false;
          const modalRef = this.modalService.open(ReceiptDetailsComponent, { size: 'xl', });
          modalRef.componentInstance.orderPaymentTypes = [...response.data];
          modalRef.componentInstance.paymentDetails = paymentDetailsRes.data;
          let details = {
            collectedBy: paymentDetailsRes.data['collectedBy'],
            collectionAt: paymentDetailsRes.data['collectionAt'],
            branch: paymentDetailsRes.data['branch'],
            register: paymentDetailsRes.data['register'],
          }
          modalRef.componentInstance.detailsItem = details;
          modalRef.componentInstance.collectionId = id;
        }
      })
    });
  }
}
