import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from 'src/app/services/header.service';
import { TransactionHistoryService } from '../../../../transaction-history-services/transaction-history.service';
import * as _ from 'underscore';
import { SparePartPaymentModalComponent } from '../spare-part-payment-modal/spare-part-payment-modal.component';
import { ReceiptDetailsComponent } from '../../receipt-details/receipt-details.component';
import { CollectService } from '../../../../../../cashier/collect/services/collect.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-view-spare-parts',
  templateUrl: './view-spare-parts.component.html',
  styleUrls: ['./view-spare-parts.component.scss'],
})
export class ViewSparePartsComponent {
  searchText: string = '';
  sort: number = 1;
  totalRecords: number;
  totalRecordsSublet: number;
  pageNoSublet: number = 0;
  paginSublet: number;
  pagesSublet: any[] = [];

  /////
  totalRecordsParts: number;
  pageNoParts: number = 0;
  paginParts: number;
  pagesParts: any[] = [];
  /////
  totalRecordsLabor: number;
  pageNoLabor: number = 0;
  paginLabor: number;
  pagesLabor: any[] = [];
  provideExpenses: any;
  serviceCollectionCenterDetails: any;
  collectionsPayment: any[] = [];
  serviceSalesOrderNo: number;
  search: string;
  nameInitials: any;
  newName: any;
  serviceCenterParts: any;
  collectionId;
  retunedOrderCollectionId;
  orderVersion = 'v2';
  isReplace: boolean;
  isLoading: boolean;
  constructor(
    private route: ActivatedRoute,
    private router: Router,

    private TransactionHistoryService: TransactionHistoryService,
    private modalService: NgbModal,
    public headerService: HeaderService,
    public _collectService: CollectService,
    public _location: Location,
  ) { }
  ngOnInit(): void {
    const ids = this.route.snapshot.paramMap.get('id').split('-');
    this.collectionId = ids[0];
    this.retunedOrderCollectionId = ids[1];
    this.isReplace = JSON.parse(this.route.snapshot.paramMap.get('isReplace'));
    this.getSparePartDetails(this.collectionId);
    this.headerService.setTitle(
      'Transactions History > Spare Parts > View Spare Parts Details'
    );
  }
  goBack() {
    this._location.back();
  }
  tabChange(version) {
    this.orderVersion = version;
    this.getSparePartDetails(version == 'v2' && this.collectionId || this.retunedOrderCollectionId);
  }
  /**
   * get view details allocation
   */
  oppurtunityId: number;
  getSparePartDetails(collectionId) {
    this.TransactionHistoryService.getSparePartDetails(collectionId).subscribe(
      (response) => {
        console.log(response, 'view details Spare Parts');

        if (response.isSuccess == true) {
          this.serviceCollectionCenterDetails = response.data;
          this.oppurtunityId = response?.data?.sparePartsSalesOrderOpportunityNo;
          this.sparePartsDetailsByRuturnedOrderFlag(this.oppurtunityId);
          this.collectionsPayment = response.data.collections;
          this.totalRecords = response?.info?.totalRecordsCount;
          this.serviceSalesOrderNo = this.serviceCollectionCenterDetails?.servicesSalesOrderNo;

          this.nameInitials =
            this.serviceCollectionCenterDetails?.customer?.customerName;
          this.newName = this.nameInitials.split(' ');
          console.log(this.newName, 'name');
        }
      }
    );
  }
  sparePartsDetailsByRuturnedOrderFlag(id) {
    if (!this.isReplace) {
      this.getServiceCenterParts(id);
    }
    else {
      if (this.isReplace && this.orderVersion == 'v1') {
        this.getServiceCenterParts(id);
      }
      else {
        this.getSparePartsReturnOrdersPrts(id);
      }
    }
  }
  //service center parts
  partsList: any;
  partsData: any;
  getServiceCenterParts(id) {
    this.TransactionHistoryService.getSPParts(id, this.pageNoParts).subscribe((response) => {
      this.serviceCenterParts = response;
      if (response.isSuccess) {
        this.partsData = response;
        this.partsList = response?.data;
        this.totalRecordsParts = response?.info?.totalRecordCount;
        this.paginParts = Math.ceil(this.totalRecordsParts / 2);
        this.pagesParts = _.range(this.paginParts);
      } else {
        this.partsList = [];
      }
    }
    );
  }
  getSparePartsReturnOrdersPrts(id) {
    this._collectService.getSparePartsReturnOrdersPrts(id).subscribe((response: any) => {
      if (response.isSuccess) {
        this.partsData = response;
        this.partsList = response?.data;
        this.totalRecordsParts = response?.info?.totalRecordCount;
        this.paginParts = Math.ceil(this.totalRecordsParts / 2);
        this.pagesParts = _.range(this.paginParts);
      } else {
        this.partsList = [];
      }
    })
  }
  //openModalPayment() {
  //  this.TransactionHistoryService.getSparePartsPaymentDetails(this.collectionId).subscribe((response) => {
  //    console.log(response, 'payment spare');
  //    if (response.isSuccess == true) {
  //      const modalRef = this.modalService.open(
  //        SparePartPaymentModalComponent,
  //        {
  //          size: 'lg',
  //        }
  //      );
  //      modalRef.componentInstance.detailsItem = response.data;
  //      modalRef.componentInstance.collectionId = this.collectionId;
  //    }
  //  });
  //}
  openModalPayment(id) {
    this.isLoading = true;
    this.TransactionHistoryService.getSparePartsPaymentDetails(id).subscribe((paymentDetailsRes) => {
      this.TransactionHistoryService.getOrderCollectionPaymentTypes(id).subscribe(response => {
        console.log(paymentDetailsRes, 'payment');
        if (response.isSuccess) {
          this.isLoading = false;
          const modalRef = this.modalService.open(ReceiptDetailsComponent, { size: 'xl', });
          modalRef.componentInstance.orderPaymentTypes = [...response.data];
          modalRef.componentInstance.paymentDetails = paymentDetailsRes.data[0];
          let details = {
            collectedBy: paymentDetailsRes.data[0]['collectedBy'],
            collectionAt: paymentDetailsRes.data[0]['collectionAt'],
            branch: paymentDetailsRes.data[0]['branch'],
            register: paymentDetailsRes.data[0]['register'],
          }
          modalRef.componentInstance.detailsItem = details;
          modalRef.componentInstance.collectionId = id;
        }
      })
    });
  }
  // parts
  setPageParts(page: number) {
    this.pageNoParts = page;
    // this.getList();
    this.getServiceCenterParts(this.oppurtunityId);
  }
}
