import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SharedService } from 'src/app/services/shared.service';
import { TreasuryService } from '../../../../petty-cash-treasury/services/treasury.service';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { CommonService } from '../../../../../../../services/common.service';
@Component({
  selector: 'app-receipt-details',
  templateUrl: './receipt-details.component.html',
  styleUrls: ['./receipt-details.component.scss']
})
export class ReceiptDetailsComponent {
  @Input() detailsItem;
  @Input() orderPaymentTypes;
  @Input() collectionId;
  @Input() paymentDetails;
  @Output() sendtoLoadData = new EventEmitter();
  details: any;
  selected: number;
  orderCollectionType = 0;
  pageNo = 1;
  totalRecordCount = 0;
  pnOrderCollectionNotesList = [];
  cardsList = [];
  isCard = false;
  collectionReportParams;
  constructor(
    public _treasuryService: TreasuryService,
    public sharedService: SharedService,
    public activeModal: NgbActiveModal,
    public _activeRoute: ActivatedRoute,
    public _commonService: CommonService,
    private _location: Location
  ) { }
  ngOnInit(): void {
    this.orderCollectionType = +(localStorage.getItem('thOrderCollectionType'));
    if (this.orderCollectionType == 7005) {
      this.detailsItem = { ...this.detailsItem, collectedBy: this.detailsItem?.createdBy }
    }
    if (this.orderCollectionType == 7008) this.getPNOrderCollectionNotes();
    this.cardsList = this.orderPaymentTypes.filter(x => x.isCard);
    this._activeRoute.queryParams.subscribe(params => {
      this.collectionReportParams = params;
    })
  }
  dismissModal() {
    if (this.collectionReportParams?.type) {
      this._commonService.NavigateToRoute('/collection-report');
    }
    this.activeModal.dismiss();
  }
  chooseTab(val) {
    this.selected = val.paymentTypeId;
    this.isCard = val.isCard;
  }
  getPNOrderCollectionNotes() {
    let params = `?OrderCollectionId=${this.collectionId}&pageNo=${this.pageNo - 1}&pageSize=6`;
    this._treasuryService.getPNOrderCollectionNotes(params).subscribe((response: any) => {
      this.pnOrderCollectionNotesList = response.data;
      this.totalRecordCount = response.totalRecordCount;
    })
  }
  pageChange(page) {
    this.pageNo = page
    this.getPNOrderCollectionNotes();
  }
}
