import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TransactionsHistoryComponent } from './transactions-history.component';
import { ViewDirectPaymentsComponent } from './Transaction-history-Collections/transaction-history-collections/direct-payments/view-direct-payments/view-direct-payments.component';
import { ViewServiceCenterComponent } from './Transaction-history-Collections/transaction-history-collections/service-center/view-service-center/view-service-center.component';
import { ViewSparePartsComponent } from './Transaction-history-Collections/transaction-history-collections/spare-parts/view-spare-parts/view-spare-parts.component';
import { UnderTakingDetailsComponent } from './Transaction-history-Collections/transaction-history-collections/bank-under-taking/under-taking-details/under-taking-details.component';
import { InHouseFinanceDetailsComponent } from './Transaction-history-Collections/transaction-history-collections/in-house-financing/in-house-finance-details/in-house-finance-details.component';
import { ViewDetailsComponent } from './Transaction-history-Collections/transaction-history-collections/promissory-note/view-details/view-details.component';

const routes: Routes = [
  { path: '', component: TransactionsHistoryComponent },
  { path: 'view-direct-payment/:id', component: ViewDirectPaymentsComponent },
  { path: 'view-service-center/:id', component: ViewServiceCenterComponent },
  { path: 'view-spare-part/:id/:isReplace', component: ViewSparePartsComponent },
  { path: 'view-undertaking/:id', component: UnderTakingDetailsComponent },
  { path: 'view-finance/:id', component: InHouseFinanceDetailsComponent },
  { path: 'view-promissory-note/:id', component: ViewDetailsComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TransactionsHistoryRoutingModule {}
