import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  AbstractControl,
  FormArray,
  FormBuilder,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalPayDoneComponent } from 'src/app/dashboard/modules/cashier/collect/modal-payment/modal-pay-done/modal-pay-done.component';
import { CollectService } from 'src/app/dashboard/modules/cashier/collect/services/collect.service';
import { SharedService } from 'src/app/services/shared.service';
@Component({
  selector: 'app-other-revenue-payment-modal',
  templateUrl: './other-revenue-payment-modal.component.html',
  styleUrls: ['./other-revenue-payment-modal.component.scss'],
})
export class OtherRevenuePaymentModalComponent {
  @Input() detailsItem;
  @Input() collectionId;
  @Input() paymentDetails;
  @Output() sendtoLoadData = new EventEmitter();

  formGroup: FormGroup;
  details: any;

  isShow: boolean = true;
  items = [1, 2, 1, 2, 1, 2];
  submitted: boolean = false;
  selected: number;
  isLoading: boolean;
  errorMessage: any;

  constructor(
    private collectService: CollectService,
    public sharedService: SharedService,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private router: Router,
    private fb: FormBuilder
  ) { }
  ngOnInit(): void {
    //this component is used in two places other revenue and customer payments for showing order details......
    //so that's why below we have modified data to show in both component with same binding in view.
    let cashCollections = this.paymentDetails['ordersCashCollection']
    if (cashCollections) {
      this.paymentDetails = { ...this.paymentDetails, ordersCashCollections: cashCollections }
    }
  }
  chooseTab(val: number) {
    this.selected = val;
  }
}
