import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-rd-visa',
  templateUrl: './rd-visa.component.html',
  styleUrls: ['./rd-visa.component.scss']
})
export class RdVisaComponent {
  @Input() detailsItem;
  @Input() card;
  cardCollections = [];
  ngOnInit(): void {
    this.cardCollections = this.detailsItem['ordersCardsCollection'].filter(x => x.paymentCardTypeId == this.card.paymentTypeId);
    //this.cardCollections = this.detailsItem['ordersCardsCollection'];
  }
}
