import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-rd-express',
  templateUrl: './rd-express.component.html',
  styleUrls: ['./rd-express.component.scss']
})
export class RdExpressComponent {
  @Input() detailsItem;
  cardCollections = [];
  ngOnInit(): void {
    this.cardCollections = this.detailsItem['ordersCardsCollection'].filter(x => x.paymentCardTypeId == 13003);
  }
}
