import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-rd-master',
  templateUrl: './rd-master.component.html',
  styleUrls: ['./rd-master.component.scss']
})
export class RdMasterComponent {
  @Input() detailsItem;
  cardCollections = [];
  ngOnInit(): void {
    this.cardCollections = this.detailsItem['ordersCardsCollection'].filter(x => x.paymentCardTypeId == 13002);
  }
}
