import { Component } from '@angular/core';
import { TransactionHistoryService } from '../../transaction-history-services/transaction-history.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ProvideExpensesModalComponent } from 'src/app/dashboard/modules/main-fund/provide-expenses/provide-expenses-modal/provide-expenses-modal/provide-expenses-modal.component';
import * as _ from 'underscore';
import { HeaderService } from 'src/app/services/header.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-transaction-history-collections',
  templateUrl: './transaction-history-collections.component.html',
  styleUrls: ['./transaction-history-collections.component.scss'],
})
export class TransactionHistoryCollectionsComponent {
  selected = 7001;
  collectedAmounts: any;

  constructor(
    private TransactionHistoryService: TransactionHistoryService,
    private modalService: NgbModal,
    public headerService: HeaderService,
    public _activeRoute: ActivatedRoute,
  ) {
    ////
  }

  ngOnInit(): void {
    this.getCollectedAmount();
    localStorage.setItem('thOrderCollectionType', '7001');
    this.headerService.setTitle(
      'Transactions History > Service Center'
    );
    // This code will run when view details of revenue,transfer-fees and customer-payments from Collections Report page
    this._activeRoute.queryParams.subscribe(params => {
      this.goToPage(+params['type'])
    })
  }
  goToPage(value) {
    this.selected = value;
    localStorage.setItem('thOrderCollectionType', value);
    if (value == 7002) {
      this.headerService.setTitle(
        'Transactions History > Direct Payment Collection'
      );
    } else if (value == 7001) {
      this.headerService.setTitle('Transactions History > Service Center');
    } else if (value == 7003) {
      this.headerService.setTitle('Transactions History > Spare Parts');
    }
    else if (value == 7004) {
      this.headerService.setTitle('Transactions History > Other Revenues');
    }
    else if (value == 7005) {
      this.headerService.setTitle('Transactions History > Customer Payments');
    }
    else if (value == 7006) {
      this.headerService.setTitle('Transactions History > Bank Undertaking Sales Orders');
    }
    else if (value == 7007) {
      this.headerService.setTitle('Transactions History > In-house Financing Sales Orders');
    }
    else if (value == 7008) {
      this.headerService.setTitle('Transactions History > Promissory Notes Orders');
    }
    else if (value == 7009) {
      this.headerService.setTitle('Transactions History > Transfer Fees');
    }
  }

  /**
   * get Collected Amount
   */

  getCollectedAmount() {
    this.TransactionHistoryService.getCollectedAmount().subscribe(
      (response) => {
        if (response.isSuccess) {
          this.collectedAmounts = response?.data;
          console.log(response, 'collected Amount');
        }
      }
    );
  }
}
